// i . i 
.btn-light {
    width: 236px;
    height: 48px;
    border: 2px solid var(--color-text-light);
    border-radius: 25px;
    background-color: rgba(0, 0, 0, 0.4);
    font-family: var(--primary-fonts);
    font-weight: 700;
    font-size: 16px;
    line-height: calc(20 / 16);
    letter-spacing: 0.02em;
    color: var(--color-text-accent);

    &:hover,
    &:focus {
        background-color: var(--color-text-light);
    }

    @include tablet {
        height: 49px;
    }
}

// i .  

.btn-dark {
    width: 236px;
    height: 48px;
    border: 2px solid var(--color-text-light);
    border-radius: 25px;
    background-color: var(--color-text-primary);
    font-family: var(--primary-fonts);
    font-weight: 700;
    font-size: 16px;
    line-height: calc(20 / 16);
    letter-spacing: 0.02em;
    color: var(--color-text-accent);

    &:hover,
    &:focus {
        background-color: var(--color-text-light);
    }

    @include tablet {
        height: 49px;
    }
}

//     (   )

.reserv-btn {
    width: 100%;
    height: 64px;
    border: 1px solid var(--color-text-accent);
    border-radius: 25px;
    background-color: var(--color-text-primary);
    font-family: var(--primary-fonts);
    font-weight: 500;
    font-size: 20px;
    line-height: calc(24 / 20);
    letter-spacing: 0.02em;
    color: var(--color-text-light);

    &:hover,
    &:focus {
        background-color: var(--color-text-accent);
        color: var(--color-text-primary);
    }

    @include tablet {
        width: 434px;
        font-size: 24px;
        line-height: calc(29 / 24);
    }
}

//      i

.modal-reserv-btn {
    width: 100%;
    height: 64px;
    border: 1px solid var(--color-text-accent);
    background-color: var(--color-text-primary);
    font-family: var(--primary-fonts);
    font-weight: 500;
    font-size: 20px;
    line-height: calc(24 / 20);
    letter-spacing: 0.02em;
    color: var(--color-text-light);

    &:hover,
    &:focus {
        background-color: var(--color-text-accent);
        color: var(--color-text-primary);
    }

    @include tablet {
        font-size: 24px;
        line-height: calc(29 / 24);
    }
}